import React, { useState, useEffect } from "react";
import PieChart from "react-simple-pie-chart";
import { getAccount } from "../../services/http";
import _ from "lodash";
import Nav from "../common/nav";
import Footer from "../common/footer";
import Table from "../common/table";

const SummaryExplore = ({ account }) => {
  const [transactions, setTransactions] = useState(null);
  const [summary, setSummary] = useState(null);
  const [pieData, setPieData] = useState(null);

  useEffect(() => {
    getAccountData();
  }, []);

  const getAccountData = async () => {
    const { data } = await getAccount({ account: account });
    console.log(data);
    setSummaryData(data.summary);
    buildPieChart(data.transactions);
    setTransactions(data.transactions);
  };

  const setSummaryData = (data) => {
    setSummary(
      Object.entries(data).map((v, i) => (
        <div className="card" key={i}>
          <h3>{v[0]}</h3>
          <h3>${v[1]}</h3>
        </div>
      ))
    );
  };

  const buildPieChart = (data) => {
    let pie = [];
    // Split by source and count
    Object.values(data).forEach((v) => {
      if (v.Amount < 0) {
        pie[v.Source] ? ++pie[v.Source] : (pie[v.Source] = 1);
      }
    });
    // Set values
    let total = 0;
    let i = 0;
    Object.values(pie).forEach((v) => {
      total += v;
    });
    const colors = [
      "#069444",
      "#3EC36B",
      "#adc33e",
      "#c3a23e",
      "#3ec3be",
      "#81b1e4",
    ];

    setPieData(
      Object.entries(pie).map((v) => {
        const value = Math.round((v[1] / total) * 100);
        colors.length < i && (i = 0);
        return { color: colors[i++], value: value, source: v[0] };
      })
    );
  };

  const renderLegend = (data) => {
    return Object.values(data).map((v, i) => {
      v.source === "null" && (v.source = "Other");
      return (
        <h4 style={{ color: v.color }}>
          {v.source} <span>{v.value}%</span>
        </h4>
      );
    });
  };

  return (
    <>
      <Nav />
      <section>
        <div className="container">
          <div className="flex-wrapper mobile-apart">
            <h1>{account}</h1>
          </div>
          <hr />
          <div className="pie-chart-wrapper card">
            {pieData && (
              <>
                <PieChart slices={pieData} />
                <div>{renderLegend(pieData)}</div>
              </>
            )}
          </div>
          <div className="flex-wrapper mb-5 summary-totals">
            {summary && summary}
          </div>
          <div>
            {!_.isEmpty(transactions) && (
              <div>
                <h5>Transactions</h5>
                <Table props={transactions} />
              </div>
            )}
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};
export default SummaryExplore;
